export default {
    created() {
        
        this.getData()
    },
    data() {
        return {
            number:'',
            data: [],
            bind:"获取中...",
            outList:[],
            outServePrice:0
        };
    },
    methods: {
        outTypeValue(value){
            switch (value) {
                case 0:
                    return "未处理";
                case 1:
                    return "已处理";
                case 2:
                    return "异常";
                default:
                    return "未知"
            }
        },
        outTagType(value){
            switch (value) {
                case 0:
                    return "info";
                case 1:
                    return "success";
                case 2:
                    return "error";
                default:
                    return "info"
            }
        },
        async getData() {
            // 任务列表
            await this.$u.get('/get_assets').then(
                res => {
                    if (res.errorCode !== 0) return;
                    this.data = res.data['assets'];
                    // this.number = res.data['assets'].wallet;
                    this.bind=res.data['bind'];
                }
            )
            // 请求列表
            await this.$u.get('/get_out_order').then(res=>{
                if(res.errorCode!==0) return;
                this.outList=res.data;
            })
            // 服务费
            await this.$u.get('/get_serve_price',{name:'out'}).then(res=>this.outServePrice=(Math.min(res.data,1) * 100).toFixed(0)+'%');
            
        },
        async out() {
            if(this.$u.test.empty(this.number)){
               uni.showToast({
                   title: "请输入提现金额",
                   icon: 'none'
               })
               return; 
            }
            if(!(this.$u.test.digits(this.number))){
               uni.showToast({
                   title: "请输入正确的提现金额",
                   icon: 'none'
               })
               return; 
            }
            if(this.number>this.data.wallet){
               uni.showToast({
                   title: "余额不足提现",
                   icon: 'none'
               })
               return; 
            }
            if ((this.number % 10) != 0 || this.number == 0) {
                uni.showToast({
                    title: "提现金额必须为10倍",
                    icon: 'none'
                })
                return;
            }
            // 请求提现
            await this.$u.post('/post_out_order', {
                amount: this.number
            }).then(res => {
                {
                    if (res.errorCode !== 0) return;
                    uni.showToast({
                        title: res.msg,
                        icon: 'none'
                    })
                    return;
                }
            })


        },
        async recharge() {
            if(this.number.length!==32){
                uni.showToast({
                    title: "卡密格式不正确",
                    icon: 'none'
                })
                return;
            }
            // 请求充值
        await this.$u.post('/recharge',{'cKey':this.number}).then(res=>{
             if(res.errorCode!==0)return;
             uni.showToast({
                 title: res.msg,
                 icon: 'none'
             })
         })
        
        
        }
    }
}
